'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './pincode.events';

var PincodeSchema = new mongoose.Schema({
  pincode:{
  	type:Number
  },
  createdAt:{
  	type:Date,
  	default:Date.now
  }
});

registerEvents(PincodeSchema);
export default mongoose.model('Pincode', PincodeSchema);
